/* square buttons */
.rec.rec-arrow {
  border-radius: 0;
  background-color: #1b8f55 !important;
}
/* round buttons on hover */
.rec.rec-arrow:hover {
  border-radius: 50%;
  background-color: #105231 !important;
}
/* hide disabled buttons */
.rec.rec-arrow:disabled {
  visibility: hidden;
}
/* disable default outline on focused items */
/* add custom outline on focused items */
.rec-carousel-item:focus {
  outline: none;
  box-shadow: inset 0 0 1px 1px lightgrey;
}

.rec-dot_active {
  background-color: #1b8f55 !important;
  box-shadow: #105231 0px 0px 1px 3px !important;
}

.rec-carousel-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.foto {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px;
}

.foto img {
  width: 50%;
  height: 60%;
}

.sc-dmqHEX.iDBcKo.rec.rec-slider-container {
  width: 400px;
  height: 400px;
}

@media only screen and (max-width: 700px) {
  .sc-dmqHEX.iDBcKo.rec.rec-slider-container {
    width: 400px;
    height: 700px;
  }

  .foto img {
    width: 80%;
    height: 60%;
  }
}

#msg-convite {
  margin-top: 15px;
  background-color: #105231;
  padding: 10px;
  border-radius: 10px;
}

#modal-mensagem {
  font-family: arial;
  line-height: 25px;
  text-align: justify;
  font-weight: bolder;
  color: whitesmoke;
}
